@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300&display=swap);
input {
    font-size: 1.2rem;
    width: 450px;
    color: #666;
    padding: 0.5rem;
}

.card {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border: 1px solid #ddd;
    border-radius: 6px;
    margin-bottom: 10px;
    margin-right: 10px;
    overflow: hidden;
    max-width: 200px;
}

.card p {
    padding: 5px;
    text-align: center;
}

.card img {
    max-height: 200px;
}

.card-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
}

:root {
    --animate-delay: 3s;
}

* {
    font-family: "Lato", sans-serif;
}

body {
    padding: 20px;
    font-size: 16px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0.2rem;
}

